  border-radius: 5rem;
}
.navbar-toggler .nav-menu-icon:before, .navbar-toggler .nav-menu-icon:after, .dropdown .nav-menu-icon:before, .dropdown .nav-menu-icon:after {
  position: relative;
  display: block;
  width: 1.25rem;
  height: 3px;
  content: "";
  background-color: #FFFFFF;
  border-radius: 5rem;
}
.navbar-toggler .nav-menu-icon.bg-secondary {
  background-color: #002856;
}
.navbar-toggler .nav-menu-icon.bg-secondary:before, .navbar-toggler .nav-menu-icon.bg-secondary:after {
  background-color: #002856;
}
.navbar-toggler.collapsed .nav-menu-icon, .dropdown .nav-menu-icon {
  transition: transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.navbar-toggler.collapsed .nav-menu-icon:before, .dropdown .nav-menu-icon:before {
  top: -7px;
  transition: top 0.1s ease-in 0.25s;
}
.navbar-toggler.collapsed .nav-menu-icon:after, .dropdown .nav-menu-icon:after {
  bottom: -4px;
  transition: bottom 0.1s ease-in 0.25s, transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.navbar-toggler:not(.collapsed) .nav-menu-icon, .dropdown.show .nav-menu-icon {
  transition: transform 0.22s cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
  transform: rotate(225deg);
}
.navbar-toggler:not(.collapsed) .nav-menu-icon:before, .dropdown.show .nav-menu-icon:before {
  top: 0;
  transition: top 0.1s ease-out;
}
.navbar-toggler:not(.collapsed) .nav-menu-icon:after, .dropdown.show .nav-menu-icon:after {
  bottom: 3px;
  transition: bottom 0.1s ease-out, transform 0.22s cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
  transform: rotate(-90deg);
}

